import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
//import Box from '@material-ui/core/Box';
import { Typography } from '@material-ui/core';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Charts from './highchart';

export default function TotalAmountGrid(props) {
    const useStyles = theme => ({
        root: {
          flexGrow: 1,
        },
        paper: {
          padding: 10,
          textAlign: 'center',
          color: '#253149',
          height : 210,
          background : "#253149"
          
          
      
          
        },
      });
  const classes = useStyles();
  
  return (
    
    <div className={classes.root}>
       
      <Grid container spacing={1} >
       
        <Grid item xs={12}>
          <Paper className={classes.paper} style={{height:220,width:460,marginLeft:10,backgroundColor:"#253149"}}><Typography><p style={{color:"grey"}}>&nbsp;</p><h1 style={{color:"white" , fontSize : 60}}><Charts />
          </h1></Typography></Paper>
          
        </Grid>
       
      </Grid>
 
    </div>
  );
}
