import axios from 'axios';
import React from 'react';
import CenteredGrid from '../components/ar-status';
import {connect} from 'react-redux';
import store from './storeMessage';
import {barSelected} from '../actions/action.js'

 class CustomerInvoice extends React.Component{
    constructor(){
        super();
    this.state={
        cust : [],
        Cust_no : 0,
        
        
    };
    this.getUniqueBN =this. getUniqueBN.bind(this);
    this.getTotalOpen=this.getTotalOpen.bind(this);
    this.getAvgDaysPast=this.getAvgDaysPast.bind(this);
    this.getOpenInvoices=this.getOpenInvoices.bind(this);
    this.showall=this.showall.bind(this);
   }
   getUniqueBN() {
    const c = this.state.cust;
    const uniqueCust = [...new Set(c.map(c => c.customer_number))];

    return uniqueCust;
  }
  getOpenInvoices(){
      return this.state.cust.reduce(function(sum,cust){
          if(cust.isOpen=='1') return sum+1; else return sum;
      },0)
  }
  

  getTotalOpen=()=> this.state.cust.reduce(
      (sum,cust) => sum+cust.total_open_amount,0
  )
  getAvgDaysPast=()=> this.state.cust.reduce(
    (sum,cust) => sum+cust.dayspast_due,0
)
   /* componentDidMount(){
        axios.get(`http://localhost:8080/1705706/ServletFromJson`)
        .then(res=>{
            const cust=res.data;
            this.setState({cust});
        })
    }*/
    showall(){
        axios.get(`http://localhost:8080/1705706/ServletFromJson`)
        .then(res=>{
            const cust=res.data;
            this.setState({cust});
            console.log("all data")
        })
      
      }
    filterTable(business_code){
        //console.log("Business",business_code)
        axios.get(`http://localhost:8080/1705706/GetFromBusinessCode`,{
          params: {
                      
            business_code: business_code
          }
        })
        .then((response) => {
         
          const cust=response.data;
          this.setState({cust});
          
        })
      }
    // {uniqueCustomers.map(uniqueCustomers => <li>{uniqueCustomers}</li>)}
    render(){
       /* { if(this.props.globalState.bar!=null){
            if(this.props.globalState.bar.bar!=null){
          
           this.filterTable(this.props.globalState.bar.bar) }
           else
          {}
           }
          }*/
          { if(this.props.globalState.bar){
            if(this.props.globalState.bar.bar!=""||this.props.globalState.bar.bar!=null){
           // console.log("in table for",this.props.globalState.bar)
            if(this.props.globalState.bar.bar!=null){
           //console.log("Redux ",this.props.globalState.bar)
           this.filterTable(this.props.globalState.bar.bar) }
           else{
            //console.log("in else",this.props.globalState.bar)
           this.showall()
           }
           }}
           
          }
        const uniqueCustomers=this.getUniqueBN(); 
        const totalOpenAR=this.getTotalOpen();
        const daysPastDue=this.getAvgDaysPast();
        const totalOpenInvoice=this.getOpenInvoices();
        return(
        <div>

            <CenteredGrid number={uniqueCustomers.length} totalar={(totalOpenAR/1000000).toFixed(1)} avgdayspast={
             (daysPastDue/this.state.cust.length).toFixed(0)
            } totalopeninvoice={totalOpenInvoice} />
            
            

        </div>
        )
    }
}
const mapStateToProps = state => ({
    globalState: barSelected(state)
  })
export default connect(mapStateToProps)(CustomerInvoice)