const bar={
    bar:null
}

const message=[{
    id : "bot",
    text:" "
}]
const reducer = (state={bar: null, message: []},action) =>{
    switch(action.type){
        case  'GET_SELECTED_BAR':
          return {
                   ...state, 
            bar: action.bar
        
}
          
          /*case  'UNSELECT_BAR':
          return [
                  {
                      distCustDim: action.distCustDim
                  }
          ]*/
          case  'HANDLE_MESSAGE':
          
          return {...state,
                    
                      message: [...state.message, action.message]
                  
          }
    }
  
}

export default reducer