import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
//import Box from '@material-ui/core/Box';
import { withStyles } from '@material-ui/core/styles';
import { Theme } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import { Typography } from '@material-ui/core';

const useStyles = theme => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: 20,
      textAlign: 'center',
      color: "white",
      height : 600,
      background : "#253149"
      
      
  
      
    },
  });
export default function CenteredGrid(props) {
  
  const classes = useStyles();
  
  return (
    
    <div className={classes.root}>
   
      <Grid container spacing={1} >
       
        <Grid item xs={3}>
          <Paper className={classes.paper} style={{backgroundColor:"#253149",height:120,textAlign: 'center',}}><Typography><p style={{color:"grey"}} autoid="total-customers-text-collector">Total Customers</p><h1 style={{color:"white" , fontSize : 60}}>{props.number}
          </h1></Typography></Paper>
          
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper} style={{backgroundColor:"#253149",height:120,textAlign: 'center'}}><Typography><p style={{color:"grey"}} autoid="total-open-ar-text-collector">Total Open AR</p><h1 style={{color:"white" , fontSize : 60}}>${props.totalar}M
          </h1></Typography></Paper>
         
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper} style={{backgroundColor:"#253149",height:120,textAlign: 'center'}}><Typography><p style={{color:"grey"}} autoid="average-days-delay-text-collector">Average Days Delay</p><h1 style={{color:"white" , fontSize : 60}}>{props.avgdayspast}
          </h1></Typography></Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}  style={{backgroundColor:"#253149",height:120,textAlign: 'center'}}><Typography><p style={{color:"grey"}} autoid="total-open-invoice-text-collector">Total Open Invoice</p><h1 style={{color:"white" , fontSize : 60}}>{props.totalopeninvoice}
          </h1></Typography></Paper>
        </Grid>
      </Grid>
     
    </div>
  );
}
