import React, { Component } from 'react';
import logo from '../logo.svg';
import '../App.css';
import axios from 'axios';
import { TableCell, TableRow, Table, TableHead, TextField, TableBody } from '@material-ui/core';
import {Link} from 'react-router-dom';
import {magImg} from '../assets/mag-glass.svg';
import Grid from '@material-ui/core/Grid';

class Search extends Component {

  constructor(){
    super();

    this.state={
      search:null,
      cust:[],
      toSearch:null
    };
  }

  ToSearch=(event)=>{
    let keyword = event.target.value;
    this.setState({toSearch:keyword})
  }

  searchSpace=()=>{
    //let keyword = event.target.value;
    this.setState({search:this.state.toSearch})
  }
  componentDidMount(){
    axios.get(`http://localhost:8080/1705706/UniqueCustomers`)
    .then(res=>{
        const cust=res.data;
        this.setState({cust});
    })
}
  render(){
    const styleInfo = {
      paddingRight:'10px'
    }
    const elementStyle ={
      border:'solid',
      borderRadius:'10px',
      position:'relative',
      left:'10vh',
      height:'3vh',
      width:'20vh',
      marginTop:'5vh',
      marginBottom:'10vh'
    }
    const divStyle = {
        width:450,
        maxHeight:270,
        overflow:'scroll'

    }
    const link= {
        color: '#FFF',
        textDecoration: 'inherit',
        padding: 0
      }
    const input= {
        background : 'transparent',
        width: 340,
        height: 30,
        borderRadius: 20 ,
        outline : 'none',
        color : 'white',
        marginTop :10,
        marginLeft:30,
        postion: 'fixed',
        border: "1px solid #80dfff"
        
      }

      const styles = theme => ({
        overrides:{
        tableRow: {
          background: "white !important",
          '&:hover': {
             background: "white !important",
          }}}
        });
        
        
    const items = this.state.cust.filter((data)=>{
      
      if(this.state.search == null)
          return data
      else if(data.customer_name.toLowerCase().includes(this.state.search.toLowerCase()) || data.customer_number==parseInt(this.state.search)){
          return data
      }
    }).map(data=>{
       console.log(data.customer_name);
       
      return(
        
      
        <TableRow hover style={{wordWrap:'break-word',}}   >
            <TableCell style={styleInfo}><Link to={{pathname :"/customer-dashboard",id:{custnum:data.customer_number}}} style={link}>{data.customer_name}</Link></TableCell>
      <TableCell style={styleInfo}><Link to={{pathname :"/customer-dashboard",id:{custnum:data.customer_number}}} style={link}>{data.customer_number}</Link></TableCell>
            <TableCell style={styleInfo}><Link to={{pathname :"/customer-dashboard",id:{custnum:data.customer_number}}} style={link}>${data.total_open_amount}</Link></TableCell>
        </TableRow>
    
    
      )
    
    })

    return (
      <div style={divStyle}>
       <Grid container spacing={1} >
      <Grid>
      <input autoid="search-text-field" style={input} type="text" placeholder="Search by Customer Name or Customer Number" onChange={(e)=>this.ToSearch(e)} />
      </Grid>
      <Grid>
      <img autoid="search-icon" src={require("../assets/mag-glass.svg")} onClick={()=>this.searchSpace()} style={{height:40,width:40,marginLeft:-20,marginTop:5,postion:'fixed'}}/>
      </Grid>
      </Grid>
      <table style={{marginLeft:20,marginTop:10}}><tr style={{textAlign: 'center'},{fontSize:14}}><td style={{width:180}}>Customer Name</td><td style={{width:180}}>Customer Number</td><td>Open Amount</td></tr>
      {items}
      </table>
      </div>
      
    )
  }
}

export default Search;