import React from 'react';
import { render } from 'react-dom';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import axios from 'axios';
import crossfilter from 'crossfilter2';
import EnhancedTable from './pagination2'
import store from './storeMessage';
import { barSelected,unselect } from '../actions/action';
import {connect} from 'react-redux';


class Charts extends React.Component{

constructor(){
    super();
    this.state ={
        Total_Amount:[],
        cust:[],
        dataset:{},
        testChart:{},
        categories:[],
        data:[],
        selected:[]
        //distCust=[]
    }
    this.showChart = this.showChart.bind(this);
   // this.assign= this.assign.bind(this);
}

componentDidMount(){
    axios.get(`http://localhost:8080/1705706/UniqueCustomers`)
    .then(res=>{
        const cust=res.data;
        this.setState({cust})
    console.log("In HighCharts",cust);
    const dataset = crossfilter(cust);
    this.setState({dataset})
    const  Total_Amount = dataset.dimension(d => d.total_open_amount);
    store.dispatch(barSelected(null))
   
     
    
    const  distCust = dataset.dimension(d => d.business_code);
    this.setState({distCust})
    var groups = distCust.group().reduceSum(d => d.total_open_amount);
    this.setState({Total_Amount : groups.all()})
    store.dispatch(unselect(distCust))
    console.log("Total Amount inside then",groups.all());
    console.log("Dist Customer",distCust)
    var categories = [];
    var data	 = [];

    var gdata = groups.top(6);
    gdata.forEach(d => {
        categories.push(d.key);
        data.push(d.value);
    });
    this.setState({categories:categories})
    this.setState({data:data})
    console.log("Categories",categories,"Data",data)
  

    const options = {
        chart: {
           
          type: 'bar',
         
        },
        title: {
          text: 'My chart'
        },
       
        xAxis:{
            title : 'Total',
            title: {
                
                text: 'State',
            },				
            categories : this.state.categories,
            
        },
        series: [
          {
            data: data
          }
        ]
      };
    
    
      
     // document.getElementById("chart").innerHTML= <HighchartsReact highcharts={Highcharts} options={options} />
    })
      //this.setState({testChart})
     
    
}

// var Sum_groups = Total_Amount.group().reduceSum(d => d.actual_open_amount);
  
showChart(){
    var gotCustomer="";
const options = {
    chart: {
      type: 'bar',
      backgroundColor:'',
      gridLineWidth :0,
      minorGridLineWidth:0,
      style:{color : 'white',},
      
    },
    title: {
      text: 'Total Amount',
      style:{color : 'white',}
    },
    xAxis:{
        title : 'Total',
        style:{color : 'white',},
        minorGridLineWidth:0,
        gridLineWidth :0,
        title: {
            
            text: 'Company',
            style:{color : 'white',}
        },				
        categories : this.state.categories,
        
    },
  yAxis:{minorGridLineWidth:0,minorGridLineWidth:0},
    series: 
      {
        data: this.state.data,
        
        point:{
            events :{
               
                click: function(){
                 this.select(null,false);
                  var selectedPoints = this.series.chart.getSelectedPoints();
                  var arr=[]; 
                  /*console.log(this.props.globalState)
                  for(var i=0;i<selectedPoints.length;i++){
                      arr[i]=selectedPoints[i].category
                  } 
                  if(arr.length>0){
                    gotCustomer=selectedPoints[0].category;
                    store.dispatch(barSelected(gotCustomer))
                  
                    this.props.globalState.filter(arr);
                } else {this.props.globalState.filterAll();
                  store.dispatch(barSelected(null))
                }*/
                  if(selectedPoints[0]!=undefined){
                    console.log("let's check",selectedPoints)
                  gotCustomer=selectedPoints[0].category;
                  store.dispatch(barSelected(gotCustomer))
                
                  console.log("Selected",selectedPoints[0].category,gotCustomer);
                  }
                  else{
                    console.log("Got you")
                   // window.location.reload()
                   store.dispatch(barSelected(null))
                  }
                
              
                 
                }
                
            
            }}
      }
    
    
  };
 
  console.log(gotCustomer)
  var cust_num=this.state.cust.map(d=>{if(d.customer_name==this.state.selected) return d.customer_number})
  console.log("Customer Number",cust_num);
 return (
     
    <div style={{height:240,marginLeft:4,marginTop:80,overflow:'scroll',width:450,scrollbarWidth:5}}>

{console.log("Inside Return",this.state.data,this.state.categories)}
      <HighchartsReact highcharts={Highcharts} options={options} style={{color:"white"}} autoid="companycode-chart"/>
    </div>

  );
  //this.setState({testChart})
  //console.log("Total Amount 2",this.state.Total_Amount)
  }

  render(){
  return(
      <div style={{width:450,height:0,marginTop:-100}} id="chart">
  {console.log("Total Amount 2",this.state.Total_Amount)}
 
  <this.showChart />
 
    </div>
    );

  }
}

const mapStateToProps = state => ({
  globalState: unselect(state)
})

export default connect(mapStateToProps)(Charts);