import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
//import Box from '@material-ui/core/Box';
import { Typography } from '@material-ui/core';


import EnhancedTable from '../components/pagination2';
//import CustomPaginationActionsTable from './pagination';


export default function TableGrid(props) {
    const useStyles = theme => ({
        root: {
          flexGrow: 1,
        },
        paper: {
          padding: 10,
          textAlign: 'center',
          color: "white",
          height : 460,
          background : "#253149"
          
          
      
          
        },
      });
  const classes = useStyles();
  
  return (
    
    <div className={classes.root}>
      
      <Grid container spacing={1} >
       
        <Grid item xs={25}>
          <Paper className={classes.paper} style={{width:1040,marginTop:-235,backgroundColor: "#253149",marginLeft:480,height:400}}><Typography><p style={{color:"grey"}}></p><EnhancedTable /><h1 style={{color:"white" , fontSize : 60}}>
          </h1></Typography></Paper>
          
        </Grid>
       
      </Grid>

    </div>
  );
}
