import React ,{ Component } from 'react';
import logo from '../logo.svg';
import '../App.css';
import Axios from 'axios';
import Chat from '../components/chat.js'
import Typography from '@material-ui/core/Typography';
import CustomerInvoice from '../components/import'
import TableGrid from '../components/table.js'
import TotalAmountGrid from '../components/totalAmount.js'

import {FredaButton} from '../assets/FredaButton.png'
import Grid from '@material-ui/core/Grid';


import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';

import SearchGrid from '../components/searchgrid';


class CollectorDashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      response: 0,
      redirect: false,
      loading: false,
      isChat : false,
      newMessage:"",
    
    };
    this.handleNameChange = this.handleNameChange.bind(this);
    this.handleChat = this.handleChat.bind(this);
  }

  handleNameChange(e) {
    this.setState({
      name: e.target.value,
    });
  }

  handleChat=()=>{
    if(this.state.isChat==false){
    this.setState({
      isChat:true
    })
  }
  else{
    
    this.setState({
      isChat:false
    })
  }
  console.log(this.state.isChat)
  }
  
    
  
 
  

  render() {
    const { classes } = this.props;
    return (
      <div className="First-Div">
      <header position="sticky" style={{ backgroundColor: '#091b3' }}>
      <Grid container spacing={1} >
      <Grid>
                  <img src={require("../assets/companyLogo.svg")}/>
                  </Grid>
                     <Grid display="flex">
                  <Typography style={{color: "white",fontSize:25}}>ABC Company</Typography>
                    <Grid><h4 style={{padding : 5 ,marginTop:0,position:'fixed'}}></h4></Grid> 
                  </Grid>
  
                  <Grid>
                  <p style={{marginTop:0,marginLeft:500,backgroundColor:"#e66f0e",position:"absolute",color:'white'}}>Recievables Dashboard</p>
                  </Grid>
  
                   <Grid>
                    <div onClick={this.handleChat} autoid='professor-button'>
                   <text style={{marginTop:10,marginLeft:1150,height:35,width:120,position:"absolute",color:'white',  borderRadius: 20 ,  outline :  '#e66f0e',backgroundColor:'#e66f0e',
                   border: "2px solid #e66f0e"}}>PROFESSOR</text>
                  <img  src={require("../assets/voiceIcon.svg")}   style={{marginTop:10,marginLeft:1200,height:35,width:120,position:"absolute"}} />
                  </div>
                  </Grid>
      </Grid>
          </header>
     
          <CustomerInvoice />
         
          
          
          <div className="rowC">
          <div display="flex" flexDirection="column" style={{width:500}}>
         
          <TotalAmountGrid />
          <div style={{marginTop:0,position:"absolute"}}> 
          <SearchGrid /></div>
          </div>
          <div alignSelf="flex-end">
          <TableGrid />
          
          </div>
          </div>
          <footer style={{color:'white' ,position: 'absolute',marginTop:110,backgroundColor:'#091b3', right: 10}}>All Rights Reserved@HighRadius 2020</footer>
          <br></br>
          
          
          
      
       <script>
         
       </script>
      <br></br>
        {this.state.isChat?<Chat chat={this.state.isChat} chatting={this.handleChat} />:null}
      </div>
    );
  }
}

export default CollectorDashboard;
