import React from 'react';
import Axios from 'axios';
import voiceIcon from '../assets/voiceIcon.svg';
import send from '../assets/send-24px.svg';
import store from './storeMessage';
import { barSelected,unselect,message} from '../actions/action';
import {connect} from 'react-redux';


class Chat extends React.Component{
constructor(props){
    super(props);
        this.state={
            newMessage:"",
            isUser: 'left'
        }
        this.handleChange = this.handleChange.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
        this.isUser= this.isUser.bind(this);
        
    
}
componentDidMount(){


    Axios.post(`http://localhost:4000/chat`, {message:'hi'},
    {headers: { 'Content-Type': 'application/json' }}
    ).then((response) => {
        console.log(response);
      }, (error) => {
        console.log(error);
        
      });
      console.log("Check :",this.props.globalState)
    if(this.props.globalState.message){
      if(this.props.globalState.message.message!=null){
      console.log("Hist",this.props.globalState.message.message)
      this.props.globalState.message.message.map(message=>{
        console.log("in map",message)
      
        if(message.id=='human'){
          console.log("Message",message)
          var div= document.createElement("div",{style: {color: "red", textAlign:'right'}}); 
      div.style.textAlign='right'
      div.innerHTML = "<br>"+message.text+"</br>"
      //store.dispatch(message({id:'human' , text: this.state.newMessage}))
      document.getElementById('showMessage').appendChild(div); 
        }
        else{
          var div= document.createElement("div",{style: {color: "red", textAlign:'right'}}); 
          div.innerHTML =  "<div ><br>"+message.text+"</br></div>";
      
          document.getElementById('showMessage').appendChild(div); 

        }
      })
     /* var div= document.createElement("div",{style: {color: "red", textAlign:'right'}}); 
      div.style.textAlign='right'
      div.innerHTML = "<br>"+this.props.globalState.message[0].message.text+"</br>"
      //store.dispatch(message({id:'human' , text: this.state.newMessage}))
      document.getElementById('showMessage').appendChild(div); */
    }}
}
isUser(){
    if(this.state.isUser=='left')
        return 'right'
    else 
        return'left'
}
sendMessage(){
  document.getElementById('inputID').value = '';
    var div= document.createElement("div",{style: {color: "red", textAlign:'right'}}); 
    // and give it some content 
    div.style.textAlign='right'
    var img = document.createElement("img");
    img.setAttribute('src', "../assets/voiceIcon.svg");
    div.innerHTML = "<br autoid=\"human\">"+this.state.newMessage+"</br>"
    store.dispatch(message({id:'human' , text: this.state.newMessage}))
    document.getElementById('showMessage').appendChild(div)
    Axios.post(`http://localhost:4000/chat`, {message:this.state.newMessage},
    {headers: { 'Content-Type': 'application/json' }}
    ).then((response) => {
        console.log(response);
        var div= document.createElement("div"); 
        store.dispatch(message({id:'bot' , text: response.data.message}))
        // and give it some content 
        div.innerHTML =  "<div ><br autoid=\"ai\">"+response.data.message+"</br></div>";
      
        document.getElementById('showMessage').appendChild(div); 
    
        
      }, (error) => {
        console.log(error);
        
      });
    
    
}
handleChange(e){
    this.state.newMessage=e.target.value
}

render(){
    const input= {
        background : 'transparent',
        width: 250,
        height: 30,
        borderRadius: 20 ,
        outline : 'none',
        color : 'white',
        outline :  '#80dfff',
        border: "2px solid #80dfff"
        
      }

      const sendbutton= {
        backgroundColor : '#80dfff',
        width: 40,
        height: 40,
        borderRadius: 100,
        outline : 'blue',
        color : 'white',
        marginLeft:-35,
        marginTop:-5,
        position : 'absolute'
        
      }
    {console.log("Printing to Check",this.props.globalState);}
    return <div id="chat" style={{width:300,right:0,top:70,height:650,border:'2px solid orange', backgroundColor:'#253149',color:"white",position:"fixed"}}>
        <button autoid="professor-close-button" onClick={this.props.chatting} style={{marginLeft:265,backgroundColor:'#253149',color:'white',width:30,height:30,fontSize:25}}>X</button>
        
        <div id="showMessage" style={{wordWrap:"break-word",height:550,marginTop:0,position:"absolute",width:280 ,marginTop:20,marginLeft:10,marginRight:10,overflow:'scroll'}}>
        <img src={voiceIcon} />Hi John , How can I help you?
        </div> 
        
        
        <div id="myM" style={{textAlign:'right'}}></div>
        <div style={{bottom:2,marginLeft:10,position:"absolute"}}>
      
        <input autoid="professor-input-box" id="inputID" style={input} onChange={(e)=>this.handleChange(e)}></input><img autoid="professor-send-button" src={send} style={sendbutton} onClick={this.sendMessage} />
       
        </div>
    </div>

}
}

const mapStateToProps = state => ({
  globalState: message(state)
})

export default connect(mapStateToProps)(Chat);
