import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
//import Box from '@material-ui/core/Box';
import { Typography } from '@material-ui/core';
import Search from '../components/searchtablethirdlayer';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';


export default function SearchGrid(props) {
    const useStyles = theme => ({
        root: {
          flexGrow: 1,
        },
        paper: {
          padding: 10,
          textAlign: 'center',
          color: "white",
          height : 210,
          background : "#253149"
          
          
      
          
        },
      });
  const classes = useStyles();
  
  return (
    
    <div className={classes.root}>
       
      <Grid container spacing={1} >
       
        <Grid item xs={12}>
          <Paper className={classes.paper} style={{width:460,height:280,backgroundColor:'#253149',marginLeft:10}}><Typography><p style={{color:"white"}}><Search /></p><h1 style={{color:"white" , fontSize : 60}}>{props.number}
          </h1></Typography></Paper>
          
        </Grid>
       
      </Grid>
      
    </div>
  );
}
